@import url("https://fonts.googleapis.com/css?family=Nunito+Sans:400,700");

.profileContainer {
  width: 50%;
  margin: 0 auto;
}
.profile {
  padding: 30px;
  align-content: center;
  text-align: center;
  margin-bottom: 35px;
}
.profile img {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  margin: 0 auto;
  margin-bottom: 24px;
  display: block;
}

.profile span {
  font-family: "Montserrat", sans-serif;
  font-weight: 300;
  font-size: 1.6rem;
}

.redBracket {
  color: rgb(231, 8, 8);
  font-weight: 600 !important;
  font-size: 2rem;
}

.info {
  font-family: "Montserrat", sans-serif;
  font-weight: 300;
  font-size: 1rem;
}

@media (max-width: 567px) {
  .profileContainer {
    width: 80%;
    margin: 0 auto;
  }
}
