/*expertise div*/
.expertise {
  width: 70%;
  margin-left: 20%;
  margin-top: 2%;
}
.expertise-category {
  float: left;
  min-height: 160px;
  width: 80%;
  display: flex;
  flex-direction: column;
  padding-bottom: 40px;
}
.expertise-category span {
  margin-top: auto;
  font-size: 16px;
  font-weight: bold;
  margin-right: -5px;
}

.description {
  min-height: 150px;
  display: flex;
  flex-direction: column;
  padding-bottom: 20px;
  width: 300px;
}
.description span {
  margin-top: auto;
  font-size: 14px;
}

.bar {
  margin: auto;
  height: 0;
  width: 0;
  overflow: hidden;
}

@keyframes expertise-bar-animation-keyframe {
  0% {
    height: 0;
    width: 0;
  }
  100% {
    width: 5px;
    height: 140px;
  }
}
.animated-delay-0 {
  animation: 1.5s forwards 0s expertise-bar-animation-keyframe;
  width: 0px;
}
.animated-delay-1-5 {
  animation: 1.5s forwards 1.5s expertise-bar-animation-keyframe;
  width: 0px;
}
.animated-delay-3 {
  animation: 1.5s forwards 3s expertise-bar-animation-keyframe;
  width: 0px;
}
.animated-delay-4-5 {
  animation: 1.5s forwards 4.5s expertise-bar-animation-keyframe;
  width: 0px;
}

.brief {
  background-color: #fba853;
}
.insights {
  background-color: #35c1d1;
}
.strategy {
  background-color: #becd30;
}
.implementation {
  background-color: #ed193a;
}

.row {
  border: #ccc 1px dashed;
  width: 90%;
}

/* Bootstrap */

.row {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  /* margin-right: -15px;
  margin-left: -15px; */
}
.col-6 {
  position: relative;
  width: 100%;
  min-height: 1px;
  padding-right: 15px;
  padding-left: 25px;
  -webkit-box-flex: 0;
  -ms-flex: 0 0 50%;
  flex: 0 0 50%;
  max-width: 50%;
}
.text-right {
  text-align: right !important;
}
.pt-2,
.py-2 {
  padding-top: 0.5rem !important;
}

@media (max-width: 567px) {
  .expertise {
    width: 90%;
    margin-left: 10%;
    margin-top: 2%;
  }
  .pt-2,
  .py-2 {
    padding-top: 0.3rem !important;
  }
  .expertise-category span {
    font-size: 14px;
  }
}
