@import url("fonts.css");
@import url("font-awesome/css/font-awesome.min.css");

html,
body,
div {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

/* Links transition and colors  and remove underline*/
a,
a:visited {
  text-decoration: none !important;
  outline: 0;
  color: rgb(160, 166, 167);

  -webkit-transition: color 0.3s ease-in-out;
  -moz-transition: color 0.3s ease-in-out;
  -o-transition: color 0.3s ease-in-out;
  transition: color 0.3s ease-in-out;
}
a:hover,
a:focus {
  color: #ff3434 !important;
}
p a,
p a:visited {
  line-height: inherit;
}

.current a {
  color: #ff3434;
}

#nav li {
  font-size: 20px;
}

img {
  max-width: 100%;
  height: auto;
}
img.pull-right {
  margin: 12px 0px 0px 18px;
}
img.pull-left {
  margin: 12px 18px 0px 0px;
}

.text-center {
  text-align: center;
}

/* Portfolio Content  Sign on Image Links and Font styles of them*/

/* portfolio card first line */
.portfolio-item-meta h5 a {
  font: 14px/18px notosans-bold, sans-serif;
  color: #11abb0;
  margin-left: 7%;
}

/* portfolio card second line */
.portfolio-item .portfolio-item-meta p {
  font: 12px/18px notosans-regular, sans-serif;
  color: #c6c7c7;
  margin-bottom: 0;
  margin-left: 7%;
}

/* on hover */
.portfolio-item:hover .item-wrap {
  background-color: #3a3434;
}
.portfolio-item:hover h5 a {
  color: #fff !important;
}
.portfolio-item:hover .overlay {
  opacity: 1;
  -moz-opacity: 1;
  filter: alpha(opacity=100);
}

/* Portfolio Content  Sign on Image Links and Font styles of them*/
#portfolio-wrapper {
  margin-top: 36px;
}
#portfolio-wrapper .columns {
  margin-bottom: 36px;
}

.portfolio-item .item-wrap {
  background: #fff;
  overflow: hidden;
  border-radius: 3px;

  -webkit-box-shadow: 0px 1px 1px rgba(50, 50, 50, 0.1);
  -moz-box-shadow: 0px 1px 1px rgba(50, 50, 50, 0.1);
  box-shadow: 0px 1px 1px rgba(50, 50, 50, 0.1);

  -webkit-transition: all 0.2s ease-in-out;
  -moz-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  -ms-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}
.portfolio-item .item-wrap a {
  position: relative;
  display: block;
}

/* overlay */
.portfolio-item .item-wrap .overlay {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;

  opacity: 0;
  -moz-opacity: 0;
  filter: alpha(opacity=0);

  -webkit-transition: opacity 0.2s ease-in-out;
  -moz-transition: opacity 0.2s ease-in-out;
  -o-transition: opacity 0.2s ease-in-out;
  transition: opacity 0.2s ease-in-out;

  border-radius: 3px 3px 0 0;
  background: url(../images/overlay.png) repeat;
}
.portfolio-item .item-wrap .link-icon {
  display: block;
  color: #fff;
  font-size: 40px;
  line-height: 40px;
  text-align: center;

  opacity: 0;
  -moz-opacity: 0;
  filter: alpha(opacity=0);

  -webkit-transition: opacity 0.2s ease-in-out;
  -moz-transition: opacity 0.2s ease-in-out;
  -o-transition: opacity 0.2s ease-in-out;
  transition: opacity 0.2s ease-in-out;

  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -18.5px;
  margin-top: -20px;
}

.portfolio-item .item-wrap img {
  vertical-align: bottom;
  border-radius: 3px 3px 0 0;
}

.portfolio-item .portfolio-item-meta {
  padding: 6px 0;
}
.portfolio-item .portfolio-item-meta h5 a {
  font: 14px/18px notosans-bold, sans-serif;
  color: #11abb0;
  margin-left: 7%;
}
.portfolio-item .portfolio-item-meta p {
  font: 12px/18px notosans-regular, sans-serif;
  color: #c6c7c7;
  margin-bottom: 0;
  margin-left: 7%;
}

/* on hover */
.portfolio-item:hover .item-wrap {
  background-color: #3a3434;
}
.portfolio-item:hover h5 a {
  color: #fff !important;
}
.portfolio-item:hover .overlay {
  opacity: 1;
  -moz-opacity: 1;
  filter: alpha(opacity=100);
}
.portfolio-item:hover .link-icon {
  opacity: 1;
  -moz-opacity: 1;
  filter: alpha(opacity=100);
}

/* Content */
.portfolio-item {
  width: 18%;

  background: #fff;
  overflow: hidden;
  border-radius: 3px;
  position: relative;
  display: block;
  margin: 0 1% 0 1%;
  margin-bottom: 36px;
  -moz-box-shadow: 0px 1px 1px #ccc;
  -webkit-box-shadow: 0px 1px 1px #ccc;
  box-shadow: 0px 1px 1px #ccc;
}

.content {
  margin: 0 auto;
  background: #fff;

  display: flex;
  justify-content: center;
  align-items: center;
}

#logos {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 85%;
  height: 6%;
  margin: 0 auto;
  background-color: #3c3636;
  border-radius: 10px;
}
#logos img {
  margin: 1% 1% 1% 1%;
  width: 11%;
}

/*scrollbar style*/
body::-webkit-scrollbar {
  width: 6px;
}
body::-webkit-scrollbar-thumb {
  background: #666;
  border-radius: 20px;
}
body::-webkit-scrollbar-track {
  background: #ffffff;
  border-radius: 20px;
}

footer {
  margin: 3%;
  margin-bottom: 4%;
  text-align: center;
}

footer span {
  font: notosans-bold, sans-serif;
  font-size: 18px;
  font-weight: bold;
  color: #11abb0;
}

@media (max-width: 567px) {
  #page-title {
    width: 60%;
    height: 3%;
    padding-top: 0%;
    margin-top: 2em;
  }

  #page-title p {
    font-size: 9px;
    margin: 6px 4%;
  }

  #page-title h1 {
    font-size: 15px;
    margin-block-end: 0;
  }

  .portfolio-item {
    width: 90%;
  }

  .portfolio-item .portfolio-item-meta {
    padding: 2px 0;
  }

  .portfolio-item .portfolio-item-meta h5 a {
    font: 9px/12px notosans, sans-serif;
    color: #11abb0;
    margin-left: 7%;
    margin-block-start: 1%;
    margin-block-end: 1%;
  }

  .portfolio-item .portfolio-item-meta p {
    font: 9px/12px notosans-regular, sans-serif;
    color: #c6c7c7;
    margin-bottom: 0;
    margin-left: 1%;
  }
  #nav li {
    font-size: 10px;
  }
  ul#nav {
    min-height: 0;
  }
  #nav-wrap {
    margin-top: 0;
    margin-right: 12px;
  }
}
